
.content {
    background-color: #fff;
    padding: 48px 62px;
    padding-bottom: 102px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 47px;
    row-gap: 65px;
    position: relative;
}
.loader {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
}