
.cat {
    width: 225px;
    height: 225px;
    cursor: pointer;
    position: relative;
    transition: all .2s ;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &::before {
        display: none;
        content: '';
        position: absolute;
        right: 21px;
        bottom: 28px;
        width: 40px;
        height: 36.7px;
    }

    &:hover {
        box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24), 0px 9px 18px rgba(0, 0, 0, 0.18);
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 58.85%);
        width: 256px;
        height: 256px;
        margin-left: -15.5px;
        margin-right: -15.5px;
        margin-bottom: -31px;
        &::before {
            display: block;
            background: url('../../image/empty.png');
        }
    }
    &:nth-child(5n-4) {
        margin-left: 0px;
        &:hover {
            margin-right: -31px;
        }
    }
    .heart {
        position: absolute;
        right: 21px;
        bottom: 28px;
        width: 40px;
        height: 36.7px;
    }
}


@media (max-width: 1450px) {
    .cat {
        width: 200px;
        height: 200px;
        &:hover {
            width: 231px;
            height: 231px;
        }
    }
}
@media (max-width: 1260px) {
    .cat {
        width: 225px;
        height: 225px;
        &:hover {
            width: 256px;
            height: 256px;
        }
    }
}
@media (max-width: 943px) {
    .cat {
        width: 200px;
        height: 200px;
        &:hover {
            width: 230px;
            height: 230px;
        }
    }
}
