.header {
    background: #2196F3;
    display: flex;
    height: 64px;
    align-items: center;
    &__buttons {
        display: flex;
    }
    .button {
        background: transparent;
        border: none;
        cursor: pointer;
        a {
            padding:  21px;
            text-decoration: none;
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            text-align: center;
            letter-spacing: 0.25px;
            color: #FFFFFF;
            &.active {
                background: #1E88E5;
            }
        }
    }
}


@media (max-width : 450px) {
    .header {
        .button a {
            font-size: 13px;
            padding: 21px;
        }
    }

}